"use client";

import { useEffect, useState } from "react";
import { useRouter } from "next/navigation";
import axios from "axios";

// =============================
// Type Definition
// =============================
interface TerminalCounts {
  citybank: number;
  pbl: number;
  ibbl: number;
  mtb: number;
  sdb: number;
}

// =============================
// Component
// =============================
export default function HomePage() {
  const [counts, setCounts] = useState<TerminalCounts | null>(null);
  const [loading, setLoading] = useState(true);
  const router = useRouter();

  // Fetch terminal counts from FastAPI
  useEffect(() => {
    axios
      .get("http://localhost:8000/terminals/counts")
      .then((res) => {
        setCounts(res.data);
        setLoading(false);
      })
      .catch((err) => {
        console.error("Error fetching terminal counts:", err);
        setLoading(false);
      });
  }, []);

  // Button Configurations
  const buttons = [
    {
      name: "CityBank Terminals",
      gradient: "from-blue-400 via-blue-500 to-blue-600",
      value: counts?.citybank,
      route: "/citybank",
    },
    {
      name: "PBL Terminals",
      gradient: "from-emerald-400 via-emerald-500 to-emerald-600",
      value: counts?.pbl,
      route: "/pbl",
    },
    {
      name: "IBBL Terminals",
      gradient: "from-teal-400 via-teal-500 to-teal-600",
      value: counts?.ibbl,
      route: "/ibbl",
    },
    {
      name: "MTB Terminals",
      gradient: "from-amber-400 via-amber-500 to-amber-600",
      value: counts?.mtb,
      route: "/mtb",
    },
    {
      name: "SDB Terminals",
      gradient: "from-purple-400 via-purple-500 to-purple-600",
      value: counts?.sdb,
      route: "/sdb",
    },
  ];

  return (
    <div className="min-h-screen flex flex-col items-center bg-gradient-to-b from-gray-100 to-gray-200">
      {/* ================= Header ================= */}
      <header className="w-full bg-white shadow-md flex flex-col items-center py-2">
        <h1 className="text-xl font-bold text-gray-700 tracking-wide text-center">
          BGI Inventory Management
        </h1>
        <img
          src="/BGI-logo.jpg"
          alt="BGI Logo"
          className="h-24 object-contain mt-1"
        />
      </header>

      {/* ================= Main Section ================= */}
      <main className="flex-1 flex flex-col justify-center items-center w-full p-6">
        {loading ? (
          <p className="text-gray-600 text-lg animate-pulse">
            Loading terminal data...
          </p>
        ) : (
          <div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 gap-8 mt-6 max-w-6xl">
            {buttons.map((btn) => (
              <div
                key={btn.name}
                className={`
                  relative bg-gradient-to-br ${btn.gradient}
                  text-white rounded-3xl shadow-lg p-10 
                  flex flex-col items-center justify-center
                  hover:scale-105 hover:shadow-2xl transform transition duration-300 
                  cursor-pointer overflow-hidden
                `}
                onClick={() => router.push(btn.route || "/")}
              >
                {/* Glow Effect */}
                <span className="absolute inset-0 bg-white opacity-10 blur-3xl rounded-3xl animate-pulse-slow"></span>

                <h2 className="text-2xl font-semibold mb-2 text-center drop-shadow-sm">
                  {btn.name}
                </h2>
                <p className="text-5xl font-bold drop-shadow-md">
                  {btn.value !== undefined ? btn.value : "-"}
                </p>
              </div>
            ))}
          </div>
        )}
      </main>

      {/* ================= Footer ================= */}
      <footer className="w-full py-8 bg-white border-t flex flex-col items-center">
        <div className="flex flex-wrap justify-center gap-8 mb-4">
          {["/photo1.jpeg", "/photo2.jpeg", "/photo3.jpeg", "/footer4.png", "/footer4.png"].map(
            (src, idx) => (
              <img
                key={idx}
                src={src}
                alt={`Profile ${idx + 1}`}
                className="h-20 w-20 rounded-full object-cover border-4 border-gray-200 shadow-md transform transition-transform duration-300 ease-in-out hover:scale-125 hover:shadow-2xl"
              />
            )
          )}
        </div>

        <p className="text-gray-500 text-sm text-center">
          © {new Date().getFullYear()} BGI • All Rights Reserved
        </p>
      </footer>
    </div>
  );
}
